import { Component } from "react";
import apiService from "./services/apiService";
import Modal from "./components/Modal";
import ImageGallery from "./components/ImageGallery";
import ImageGalleryItem from "./components/ImageGalleryItem/ImageGalleryItem";
import Spinner from "./components/Loader";
import Searchbar from "./components/Searchbar";
import Button from "./components/Button";

import styles from "./App.module.css";

const MODAL = {
  NONE: "none",
  OPEN: "open",
  EDIT: "edit",
  DELETE: "delete",
};

class App extends Component {
  state = {
    // isShowModal: false,
    openedModal: MODAL.NONE,
    hits: [],
    currentPage: 1,
    searchQuery: "",
    largeImage: "",
    isLoading: false,
    smallImage: "",
    alt: "",
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevState.searchQuery !== this.state.searchQuery) {
      this.getDataApi();
    }
  }

  getDataApi = () => {
    const { currentPage, searchQuery } = this.state;
    const options = { currentPage, searchQuery };

    this.setState({ isLoading: true });

    apiService
      .fetchHits(options)
      .then((hits) => {
        this.setState((prevState) => ({
          hits: [...prevState.hits, ...hits],
          currentPage: prevState.currentPage + 1,
        }));
      })
      .catch((error) => this.setState({ error }))
      .finally(() => {
        this.setState({ isLoading: false });

        window.scrollTo({
          top: document.documentElement.scrollHeight,
          behavior: "smooth",
        });
      });
  };

  handleSearch = (searchQuery) => {
    this.setState({
      searchQuery: searchQuery,
      currentPage: 1,
      hits: [],
    });
  };

  // toggleModal = () => {
  //   this.setState(({ isShowModal }) => ({
  //     isShowModal: !isShowModal,
  //   }));
  // };

  // openModal = (img) => {
  //   this.setState({ largeImage: img });
  //   this.toggleModal();
  // };

  openModal = (img) =>
    this.setState({
      openedModal: MODAL.OPEN,
      largeImage: img,
    });

  closeModal = () =>
    this.setState({
      openedModal: MODAL.NONE,
      // activeCity: '',
    });

  render() {
    const { openedModal, isLoading, hits, smallImage, alt } = this.state;
    const shouldRenderLoadMoreButton = hits.length > 0 && !isLoading;

    return (
      <div className={styles.app}>
        <Searchbar onSubmit={this.handleSearch} />
        {hits.length > 0 && (
          <ImageGallery hits={hits} onClick={this.openModal} />
        )}
        {isLoading && <Spinner />}
        {shouldRenderLoadMoreButton && <Button onClick={this.getDataApi} />}

        {openedModal === MODAL.OPEN && (
          <Modal
            // title="Large image"
            onClose={this.closeModal}
            // icon={pencilIcon}
          >
            <ImageGalleryItem
              onClick={this.openModal}
              smallImage={hits.webformatURL}
              largeImage={hits.largeImageURL}
              alt={hits.tags}
            />
          </Modal>
        )}

        {/* {isShowModal && (
          <Modal
            largeImage={this.state.largeImage}
            onClose={this.toggleModal}
          />
        )} */}
      </div>
    );
  }
}

export default App;
